import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { LocaldataProvider } from '../localdata/localdata';

/*
  Generated class for the HttpclientProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class HttpclientProvider {

   constructor(private http: Http, private ls: LocaldataProvider) { }
    createAuthorizationHeader(headers: Headers) {
        headers.append('Authorization', this.ls.getToken());
        headers.append('Access-Control-Allow-Origin', '*');
        headers.append('content-type','application/json');
    }
    get(url): any {
        let headers = new Headers();
        this.createAuthorizationHeader(headers);
        return this.http.get(url, {
            headers: headers
        });
    }
    post(url, data): any {
        let headers = new Headers();
        this.createAuthorizationHeader(headers);
        return this.http.post(url, data, {
            headers: headers
        });
    }

}



