import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { AlertController, LoadingController, Loading, App } from 'ionic-angular';
import { LocalstorageProvider } from '../localstorage/localstorage';

/*
  Generated class for the LocaldataProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class LocaldataProvider {
    constructor(private ls: LocalstorageProvider,
        private alertCtrl: AlertController,
        private loadingCtrl: LoadingController,
        private app: App) {
    }
    inApp(): number {
        //0 微信 1.浏览器  2 app
        var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
        if (ua.indexOf("micromessenger") > -1) {
            return 0;
        }
        else if (location.href.toLowerCase().indexOf('http') == 0) {
            return 1;
        }
        else {
            return 2;
        }
    }

///用户分享家ID
setFXFamilyId(familyid: string): void {
    this.setLocalStorageAny("FXFamilyId", familyid);
}
///用户分享家ID
getFXFamilyId(): string {
    return this.getLocalStorageAny("FXFamilyId");
}

///用户分享家ID 0家庭 1酒店
setFXFamilyType(FXFamilyType: string): void {
    this.setLocalStorageAny("FXFamilyType", FXFamilyType);
}
///用户分享家ID  0家庭 1酒店
getFXFamilyType(): string {
    return this.getLocalStorageAny("FXFamilyType");
}
    public setRoot(pag: any) {
        this.app.getRootNav().setRoot(pag);
    }
    //设置本地存储数据
    private setLocalStorage(key: string, value: string): void {
        this.ls.set(key, value);
    }
    //读取本地存储数据
    private getLocalStorage(key: string): string {
        return this.ls.get(key);
    }
    private isEmpty(value:any) {
        return Object.keys(value).length === 0;
    }

    //设置本地存储对象
    private setLocalStorageAny(key: string, value: any): void {
        this.ls.setObject(key, value);
    }
    //读取本地存储对象
    private getLocalStorageAny(key: string): any {
        return this.ls.getObject(key);
    }

    
    

    getBgNeedDown(name:string):Boolean{
        let lsBg = this.getLocalStorageAny("bg"+name);
        if(this.isEmpty(lsBg)){ //没有
            return true;
        }
        return false;
    }
    getBg(name:string):any{
        return this.getLocalStorageAny("bg"+name);
    }
    //{"name":"bgName","nativeURL","file://"}
    setBg(data:any):void{
        this.setLocalStorageAny("bg"+data.name,data);
    }

    getSceneNeedDown(name:string,olMd5:string):Boolean{
        let lsScene = this.getLocalStorageAny("scene"+name);
        if(this.isEmpty(lsScene)){ //没有
            return true;
        }else if(lsScene.md5 !== olMd5){//和传进来的网上md5不同
            return true;
        }
        return false;
    }
    getScene(name:string):any{
        return this.getLocalStorageAny("scene"+name);
    }
    setScene(data:any):void{
        this.setLocalStorageAny("scene"+data.name,data);
    }

    //获取本地缓存的设备信息
    getLocalDevice(device_id: string): any {
        //获取家信息
        let familyId = this.getFamilyId();
        let userBasicInfo = this.getUserBasicInformation();
        if (userBasicInfo && userBasicInfo.length > 0) {
            for (let family of userBasicInfo) {
                if (family.family_id === familyId) {
                    for (let fa of family.roomlist)
                        if (fa.devices != null && fa.devices != "" && fa.devices.length > 0) {
                            for (let dev of fa.devices) {
                                if (dev.device_id == device_id) {
                                    return dev;
                                }
                            }
                        }
                }
            }
        }
    }

    updateLocalDevice(device: any): any {
        //更新本地设备信息
        let familyId = this.getFamilyId();
        let userBasicInfo = this.getUserBasicInformation();
        if (userBasicInfo && userBasicInfo.length > 0) {
            outfor: for (let i = 0; i < userBasicInfo.length; i++) {
                if (userBasicInfo[i].family_id === familyId) {
                    for (let j = 0; j < userBasicInfo[i].roomlist.length; j++) {
                        if (userBasicInfo[i].roomlist[j].devices != null && userBasicInfo[i].roomlist[j].devices != "" && userBasicInfo[i].roomlist[j].devices.length > 0) {
                            for (let k = 0; k < userBasicInfo[i].roomlist[j].devices.length; k++) {
                                if (userBasicInfo[i].roomlist[j].devices[k].device_id == device.device_id) {
                                    userBasicInfo[i].roomlist[j].devices[k] = device;
                                    this.setUserBasicInformation(userBasicInfo);
                                    break outfor;
                                    //  return;
                                }
                            }
                        }
                    }
                }
            }
        }
        // for (let family of userBasicInfo) {
        //     if (family.family_id === familyId) {
        //         for (let fa of family.roomlist) {
        //             if (fa.devices != null && fa.devices != "" && fa.devices.length > 0) {
        //                 for (let dev of fa.devices) {
        //                     if (dev.device_id == device.device_id) {

        //                         dev = device;
        //                         this.setUserBasicInformation(userBasicInfo);
        //                         return;
        //                     }
        //                 }
        //             }
        //         }

        //     }
        // }

    }

    //提示框 content 显示内容 2秒关闭
    showMessage(content: string) {
        let loading = this.loadingCtrl.create({
            spinner: 'hide',
            content: `
      <div class="custom-spinner-container">
        <div class="custom-spinner-box">`+ content + `</div>
      </div>`,
            duration: 1000
        });
        loading.present();
    }
    //提示框  
    alert(title: string, message: string) {
        let prompt1 = this.alertCtrl.create({
            title: title,
            message: message,
            buttons: ["确定"]
        });
        prompt1.present();
    }

    //加载  
    loading(content: string = null): Loading {
        let loading = this.loadingCtrl.create({
            content: content || '加载中...'
        });
        loading.present();
        return loading;
    }

    Loadingclose(loading: any) {
        if (loading !== null) {
            loading.dismiss();
            loading = null;
        }
    }
    //对话选择框 
    confirm(title: string = null, message: string, functionData: any) {
        var prompt1 = this.alertCtrl.create({
            title: title || "确认信息",
            message: message,
            buttons: [
                {
                    text: "取消",
                    role: "",
                    handler: () => {
                        functionData.Callback(false);
                    }
                },
                {
                    text: "确认",
                    handler: () => {
                        functionData.Callback(true);
                    }
                }
            ]
        });
        prompt1.present();
    }

    inputconfirm(title: string, messageTitle: string, text: string, functionData: any) {

        if (text != null && text != "") {
            let alert = this.alertCtrl.create({
                title: title,
                inputs: [
                    {
                        name: 'name',
                        value: text
                        // placeholder: '输入分组名称'
                    }
                ],
                buttons: [
                    {
                        text: '取消',
                        role: '取消',
                        handler: data => {
                            functionData.Callback("");
                        }
                    },
                    {
                        text: '保存',
                        handler: data => {

                            if (data.name == null || data.name == "") {
                                this.alert("提示信息", messageTitle);
                                return;
                            } else
                                functionData.Callback(data);
                        }
                    }
                ]
            });
            alert.present();
        } else {
            let alert = this.alertCtrl.create({
                title: title,
                inputs: [
                    {
                        name: 'name',
                        placeholder: '输入分组名称'
                    }
                ],
                buttons: [
                    {
                        text: '取消',
                        role: '取消',
                        handler: data => {
                            functionData.Callback("");
                        }
                    },
                    {
                        text: '保存',
                        handler: data => {

                            if (data.name == null || data.name == "") {
                                this.alert("提示信息", messageTitle);
                                return;
                            } else
                                functionData.Callback(data);
                        }
                    }
                ]
            });
            alert.present();
        }

    }


    //获取本地token
    getToken(): string {
        return this.ls.get("token");
    };
    clearLocalstorage() {
        this.ls.remove("token");
        this.ls.remove("user");
        this.ls.remove("familyId");
    }
    //设置当前用户token
    setToken(token: string): void {
        this.ls.set("token", token);
    }

    //获取本地保存当前家ID
    getFamilyId(): number {
        return this.getLocalStorageAny("familyId");
    }
    //设置本地保存当前家ID
    setFamilyId(familyId: number): void {
        this.setLocalStorageAny("familyId", familyId);
    }

    //获取本地保存当前家的名称
    getFamilyName(): string {
        return this.getLocalStorage("familyName");
    }

    //设置本地保存当前家的名称
    setFamilyName(familyName: string): void {
        this.setLocalStorage("familyName", familyName);
    }
    //获取所有家数据
    getFamilyList(): any {
        return this.getLocalStorageAny("familylist");
    }

    //设置所有家数据
    setFamilyList(familylist: any): any {
        this.setLocalStorageAny("familylist", familylist);
    }

    //设置用户登录后的家,房间设备数据
    setUserBasicInformation(userBasicInformation: any): void {
        this.setLocalStorageAny("userBase", userBasicInformation);
    }
    //获取用户登录后的家,房间设备数据
    getUserBasicInformation(): any {
        return this.getLocalStorageAny("userBase");
    }


    //获取所有房间数据
    getRoomList(): any {
        return this.getLocalStorageAny("roomlist");
    }

    //设置所有房间数据
    setRoomList(roomlist: any): void {
        this.setLocalStorageAny("roomlist", roomlist);
    }

    //获取本地用户信息
    getUser(): any {
        return this.getLocalStorageAny("user");
    }

    //设置本地用户信息
    setUser(user: any): void {
        this.setLocalStorageAny("user", user);
    }



    //获取当前用户username
    getUserName(): string {
        return this.getUser().username;
    }

    //获取当前用户Id
    getUserId(): string {
        return this.getUser().id;
    }

    //获取所有产品数据
    getProducts(): any {
        return this.getLocalStorageAny("products");
    }

    //设置所有产品数据
    setProducts(products: any): void {
        this.setLocalStorageAny("products", products);
    }


    //当前家的管理者
    getRoomUserName(): string {
        return this.getLocalStorageAny("roomUserName");
    }
    //设置所有房间数据
    setRoomUserName(username: string): void {
        this.setLocalStorageAny("roomUserName", username);
    }
    //设置当前添加操作参数
    setS(s: number): void {
        this.setLocalStorageAny("s", s);
    }
    //设置当前添加操作参数
    getS(): number {
        var data = this.getLocalStorageAny("s");
        return data == null || data == "" ? 0 : data;
    }
    //设置当前添加操作是否已完成
    setSstate(s: string): void {
        this.setLocalStorageAny("sstate", s);
    }
    //设置当前添加操作是否已完成
    getSstate(): string {
        return this.getLocalStorageAny("sstate");
    }
    //实时天气
    setWeather(products: any): void {
        this.setLocalStorageAny("weather", products);
    }


    //实时天气
    getWeather(): string {
        return this.getLocalStorageAny("weather");
    }


    //提示左滑信息
    setPromptInformation(info: Array<string>): void {
        this.setLocalStorageAny("promptinformation", info);
    }


    //提示左滑信息
    getPromptInformation(): Array<string> {
        return this.getLocalStorageAny("promptinformation");
    }

    //设置情景播放是否打开
    setPlayScene(state:boolean):void{
        this.setLocalStorageAny("PlaySceneState",state);
    }
    getPlayScene():boolean{
        let state = this.getLocalStorageAny("PlaySceneState");
        if(state !== true && state !== false){
            this.setPlayScene(true);
            return true;
        }else
            return state;
    }

    //设置TTS播放是否打开
    setPlayTTS(state:boolean):void{
        this.setLocalStorageAny("PlayTTSState",state);
    }
    getPlayTTS():boolean{
        let state = this.getLocalStorageAny("PlayTTSState");
        if(state !== true && state !== false){
            this.setPlayTTS(true);
            return true;
        }else
            return state;
    }


    //首页列表页面显示方式 0//列表 1//九公格
    setDisplayMethod(value: number): void {
        this.setLocalStorageAny("displayMethod", value);
    }
    //首页列表页面显示方式
    getDisplayMethod(): number {
        return this.getLocalStorageAny("displayMethod");
    }

    //当前用户是否是该设备的管理员 返回true 为管理员 username参数为 设备的username
    isUserAdministrator(): boolean {
        if (this.getUserName() === this.getRoomUserName())
            return true;
        else return false;
    }

    //获取时间段
    initShowTimeStr(startTime: string, endTime: string): string {
        let str = "";
        if (startTime != "" && endTime != "") {
            if (startTime == "00:00" && endTime == "00:00") {
                str = "全天"
            }
            else {
                let start = parseInt(startTime);
                let end = parseInt(endTime);
                if (end <= start) {
                    str = startTime + "到第二天" + endTime;
                } else {
                    str = startTime + "到" + endTime;
                }
            }
        }
        return str;
    }

    timeToString(hours: number): string {
        let str = "00:00";
        let hoursStr = "";
        if (hours < 10) { hoursStr = "0" + hours; } else {
            hoursStr = hours.toString();
        }
        str = hoursStr + ":00";
        return str;
    }

    //获取选中的日期
    getSelectedMsg(data: Array<number>): string {
        var str = "";
        if (data.length == 0) {
            return "执行一次";
        }
        if (data.length == 2) {
            if ((data.find(i => i.toString() == "6") != undefined) && (data.find(i => i.toString() == "0") != undefined)) {
                return "周末";
            }
        }
        else if (data.length == 5) {
            if ((!data.find(i => i.toString() == "6")) && (!data.find(i => i.toString() == "0"))) {
                return "工作日";
            }
        }
        else if (data.length == 7) {
            return "每天";
        }
        data.forEach(element => {
            switch (element.toString()) {
                case "1":
                    str += "星期一 ";
                    break;
                case "2":
                    str += "星期二 ";
                    break;
                case "3":
                    str += "星期三 ";
                    break;
                case "4":
                    str += "星期四 ";
                    break;
                case "5":
                    str += "星期五 ";
                    break;
                case "6":
                    str += "星期六 ";
                    break;
                case "0":
                    str += "星期天 ";
                    break;
            }
        });
        return str;
    }

    //获取延时显示字符串
    delayToTimeStr(delay: number): string {
        let str = "";
        if (delay > 0) {
            let sec_num = delay; // don't forget the second param
            let hours = Math.floor(sec_num / 3600);
            let minutes = Math.floor((sec_num - (hours * 3600)) / 60);
            let seconds = sec_num - (hours * 3600) - (minutes * 60);
            let hoursStr = "";
            let minutesStr = "";
            let secondsStr = "";
            if (hours < 10) { hoursStr = "0" + hours; }
            else {
                hoursStr = hours.toString();
            }
            if (minutes < 10) { minutesStr = "0" + minutes; }
            else {
                minutesStr = minutes.toString();
            }
            if (seconds < 10) { secondsStr = "0" + seconds; }
            else {
                secondsStr = seconds.toString();
            }
            if (hours != 0) {
                str += hoursStr + '小时';
            }
            if (minutes != 0) {
                str += minutesStr + '分钟'
            }
            str += secondsStr + "秒";
        } else {
            str = "0秒";
        }
        return str;
    }

    GetAnfangIcon(scene: string): string {
        if (scene == "外出设防") {
            return "waichushefang";
        }
        else if (scene == "拆防") {
            return "chaifang";
        }
        else if (scene == "在家设防") {
            return "zaijiabufang";
        }
        else {
            return "anfang";
        }
    }

    InitAirTitle(extend_props: any): string {
        let str = "";
        console.log("extend_props");
        console.log(extend_props);
        if (extend_props.on_off) {
            str += "电源:开";
        } else {
            str += "电源:关";
        }
        str += " 温度:" + extend_props.temperature;
        let windArr = [];
        if (extend_props.ud_wind_direction == 1) {
            windArr.push("上下风向");
        }
        if (extend_props.lr_wind_direction == 1) {
            windArr.push("左右风向");
        }
        if (windArr.length > 0) {
            str += " 风向:" + windArr.join(',');
        } else {
            str += " 风向:" + "自动";
        }
        if (extend_props.mode == 1) {
            str += " 模式:自动";
        } else if (extend_props.mode == 2) {
            str += " 模式:制冷";
        } else if (extend_props.mode == 3) {
            str += " 模式:除湿";
        } else if (extend_props.mode == 4) {
            str += " 模式:送风";
        } else if (extend_props.mode == 5) {
            str += " 模式:制热";
        }
        if (extend_props.air_volume == 0) {
            str += " 风量:自动";
        } else if (extend_props.air_volume == 1) {
            str += " 风量:低";
        } else if (extend_props.air_volume == 2) {
            str += " 风量:中";
        } else if (extend_props.air_volume == 3) {
            str += " 风量:高";
        }
        return str;
    }

}



