    //获取LocaUrl Token
    getUrlToken(locaUrl: string): string {
        let index = locaUrl.indexOf("token=");
        if (index >= 0)
            return locaUrl.substring(index + 6);
        return "false";
    }
    getUrlS(locaUrl: string): string {
        let index = locaUrl.indexOf("s=");
        if (index >= 0) {
            var str = locaUrl.substring(index + 2);
            let lastIndex = str.indexOf("&");
            let lastIndex1 = str.indexOf("?");
            if (lastIndex >= 0 || lastIndex1 >= 0) {
                if (lastIndex >= 0) {
                    var str2 = str.substr(0, lastIndex);
                    var s = parseInt(str2);
                    if (!isNaN(s)) {
                        this.ls.setS(s);
                        this.ls.setSstate("0");
                    }
                    //alert(str1);
                    return str2;
                } else {
                    var str1 = str.substr(0, lastIndex1);
                    var s2 = parseInt(str1);
                    if (!isNaN(s2)) {
                        this.ls.setS(s2);
                        this.ls.setSstate("0");
                    }
                    return str1;
                }

            } else {
                var s1 = parseInt(str);
                if (!isNaN(s1)) {
                    this.ls.setS(s1);
                    this.ls.setSstate("0");
                }
                return str;
            }
        }
        return "";
    }


