import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the DelayProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class DelayProvider {

 constructor(private http: HttpclientProvider) {
        
        
    }

    //获取设备延时信息
    GetDelayInfo(deviceId:string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Delay/GetDelay/' + deviceId;
        return this.http.get(url).map(this.extractData);
    }

    CreateDelay(deviceId:string, jobId:number, schedule_time:string, quartzJobId:string, services:any, op_type:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Delay/CreateDelay';
        
        return this.http.post(url, { deviceId: deviceId, jobId:jobId,  schedule_time:schedule_time, quartzJobId:quartzJobId, services:services,op_type:op_type}).map(this.extractData);
    }

    UpdateDelayState(jobId:number, isOpen:boolean):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Delay/UpdateActive';
        return this.http.post(url, { job_id:jobId, is_active:isOpen}).map(this.extractData);
    }

    

    CancelDelay(jobId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Delay/CancelDelay';
        return this.http.post(url, { job_id:jobId}).map(this.extractData);
        
    }
    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



