import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
import { quartz_job } from '../../interfaces/equipment.model';
/*
  Generated class for the TimingProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class TimingProvider {

  constructor(private http: HttpclientProvider) {


  }

  AddTiming(deviceId: string, jobId: number, dayofweek: Array<number>, quartzJobs: Array<quartz_job>): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/CreateTiming';
    // var servicesJsonStr = JSON.stringify(services);
    return this.http.post(url, { device_id: deviceId, job_id: jobId, dayofweek: dayofweek, quartz_job: quartzJobs }).map(this.extractData);
  }

  UpdateTiming(deviceId: string, jobId: number, dayofweek: Array<number>, quartzJobs: Array<quartz_job>): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/UpdateTiming';
    return this.http.post(url, { device_id: deviceId, job_id: jobId, dayofweek: dayofweek, quartz_job: quartzJobs }).map(this.extractData);
  }

  UpdateTimingState(jobId: number, isOpen: boolean): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/UpdateActive';
    return this.http.post(url, { job_id: jobId, is_active: isOpen }).map(this.extractData);
  }

  remove(jobId: number, quartzJobId: Array<string>): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/Remove';
    return this.http.post(url, { job_id: jobId, quartzJobId: quartzJobId }).map(this.extractData);

  }

  GetTimingListById(deviceId: string): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/FindTimings/' + deviceId;
    return this.http.get(url).map(this.extractData);
  }

  GetTiming(jobId: number, deviceId: string): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Timing/GetTiming';
    return this.http.post(url, { jobId: jobId, deviceId: deviceId }).map(this.extractData);
  }


  extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}



