import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
import { LocaldataProvider } from '../localdata/localdata';
/*
  Generated class for the ExternalinterfaceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class ExternalinterfaceProvider {

  constructor(private http: HttpclientProvider,
        private ls: LocaldataProvider) {
    }

    //获取空气质量检测仪时时数据(五分钟更新一次)
    FindAirStationDate(device_id: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/airstation/FindAirStationDate';
        return this.http.post(url, { device_id: device_id }).map(this.extractData);
    }

    //接口获取实时天气
    FindWeather(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/airstation/FindWeather';
        return this.http.post(url, {}).map(this.extractData);
    }

    //接口获取实时天气
    FindChart(device_id: string, type: string, time: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/airstation/FindChart';
        return this.http.post(url, { device_id: device_id, type: type, time: time }).map(this.extractData);
    }


    ///列表滑动提示信息
    UpdatePromptInformation(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Family/UpdatePromptInformation';
        return this.http.get(url).map(this.extractData);

    }

    //列表滑动提示信息
    SlideTheMessage(val: string): void {
        //列表提示信息
        let info = this.ls.getPromptInformation();
        console.log(info);
        if (info.length === undefined) {
            info = [];
            //显示提示框
            this.UpdatePromptInformation().subscribe(data => {
                info.push(val);
                this.ls.setPromptInformation(info);
            });
        } else if (info.length < 3) {
            if (!(info.indexOf(val) >= 0))
                //显示提示框
                this.UpdatePromptInformation().subscribe(data => {
                    info.push(val);
                    this.ls.setPromptInformation(info);
                });
        }
    }


    WeatherIonic(name: string): string {
        let weatherico = "";
        switch (name) {
            case '晴天':
            case '晴':
                weatherico = "iconfont icon-qingtian";
                break;
            case '多云':
                weatherico = "iconfont icon-duoyun";
                break;
            case '大雨':
                weatherico = "iconfont icon-dayu";
                break;
            case '多云转阴':
                weatherico = "iconfont icon-duoyunzhuanyin";
                break;
            case '小雨':
                weatherico = "iconfont icon-xiaoyu";
                break;
            case '中雨':
                weatherico = "iconfont icon-zhongyu";
                break;
            case '暴雨':
                weatherico = "iconfont icon-baoyu";
                break;
            case '雾':
                weatherico = "iconfont icon-wu";
                break;
            case '雷阵雨':
                weatherico = "iconfont icon-leizhenyu";
                break;
            case '雾霾':
                weatherico = "iconfont icon-wumai";
                break;
            case '小雪':
                weatherico = "iconfont icon-xiaoxue";
                break;
            case '暴雪':
                weatherico = "iconfont icon-baoxue";
                break;
            case '冰雹':
                weatherico = "iconfont icon-bingbao";
                break;
            case '大雪':
                weatherico = "iconfont icon-daxue";
                break;
            case '雷阵雪':
                weatherico = "iconfont icon-leizhenxue";
                break;
            case '雨夹雪':
                weatherico = "iconfont icon-yujiaxue";
                break;
            case '中雪':
                weatherico = "iconfont icon-zhongxue";
                break;
            default:
                weatherico = "iconfont icon-duoyunzhuanyin";
                break;
        }
        return weatherico;

    }
    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



