import { Injectable } from '@angular/core';
import { SignalrWindow } from "../providers/constants/constants";
@Injectable()
export class ExtendedUrlImg {
    navtype: number = 0;
    constructor(private window: SignalrWindow) {
        this.navtype = this.inApp();
    }

    //获取列表图片
    getlist_64(product_id: string): string {
        if (this.navtype === 2) {
            return "assets/img/pro/" + product_id + "/list_64.jpg";
        } else
            return this.window.nativeWindow.config.cdn + "assets/img/pro/" + product_id + "/list_64.jpg";
    }

    //幕布图片
    getFixedImg(val: string): string {
        if (this.navtype === 2) {
            return "assets/img/pro/" + val;
        } else
            return this.window.nativeWindow.config.cdn + "assets/img/pro/" + val;
    }

    inApp(): number {
        //0 微信 1.浏览器  2 app
        var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
        if (ua.indexOf("micromessenger") > -1) {
            return 0;
        }
        else if (location.href.toLowerCase().indexOf('http') == 0) {
            return 1;
        }
        else {
            return 2;
        }
    }
}



