import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the DoorlockProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class DoorlockProvider {

 constructor(private http: HttpclientProvider) {
    }

    //修改设备锁 关联的用户信息
    AssociationUser(device_id: string, key_type: number, key_id: string, user_type: number, nickname: string, username: string, avator: string): any {
        let url = CONFIGURATION.baseUrls.server + "/api/door/associationuser";
        return this.http.post(url, { device_id: device_id, key_type: key_type, key_id: key_id, user_type: user_type, nickname: nickname, username: username, avator: avator }).map(this.extractData);
    }

    //删除设备锁的用户信息
    DeleteDeviceUser(device_id: string, key_type: number, key_id: string, user_type: number) {
        let url = CONFIGURATION.baseUrls.server + "/api/door/deletedeviceuser";
        return this.http.post(url, { device_id: device_id, key_type: key_type, key_id: key_id, user_type: user_type }).map(this.extractData);
    }

    //锁详细解绑门磁
    DoorKeyUnBindMagnet(doorid: string) {
        let url = CONFIGURATION.baseUrls.server + "/api/device/doorkeyunbindmagnet";
        return this.http.post(url, { doorid: doorid }).map(this.extractData);
    }

    //锁详细绑定门磁
    DoorKeyBindMagnet(doorid: string, magnetid: string) {
        let url = CONFIGURATION.baseUrls.server + "/api/device/doorKeybindmagnet";
        return this.http.post(url, { doorid: doorid, magnetid: magnetid }).map(this.extractData);
    }




    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



