import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the UsercenterProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class UsercenterProvider {

    constructor(private http: HttpclientProvider) {


    }
    EditShareState(familyId: string, expires: string, enabled: number):any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/EditShareState/'+familyId;
        return this.http.post(me, { expires: expires, enabled: enabled }).map(this.extractData);
    }
    GetShareInfo(familyId: string):any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/GetShareInfo/'+familyId;
        return this.http.post(me, {}).map(this.extractData);
    }
    GetFamily(familyId: number):any {
        let me = CONFIGURATION.baseUrls.server + '/api/Family/GetFamily/'+familyId;
        return this.http.post(me, {}).map(this.extractData);
    }
    // private handleError(error: any): Promise<any> {
    //     console.error('An error occurred', error); // for demo purposes only
    //     return Promise.reject(error.message || error);
    // }
    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
    //发送短信验证码 scene //场景id:1注册时首次验证手机，2修改绑定手机，3验证新手机{token = "13413"}
    SendCode(phonenumber: string, scene: number): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/SendCode';
        return this.http.post(me, { mobile: phonenumber, scene: scene }).map(this.extractData);
    }
    //发送短信验证码 scene //场景id:0注册，1修改资料,2登录
    VerifyCode(code: string, scene: number, token: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/VerifyCode';
        return this.http.post(me, { code: code, scene: scene, token: token }).map(this.extractData);
    }
    FeedBack(topic: string, content: string) {

        let url = CONFIGURATION.baseUrls.server + '/api/Account/feedback';
        return this.http.post(url, { topic: topic, content: content }).map(this.extractData);
    }
    //修改昵称
    SetNickName(nickname: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/SetNickName';
        return this.http.post(me, { nickname: nickname }).map(this.extractData);
    }
    //修改紧急联系人
    SetEmergencymobile(emergencymobile: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/setemergencymobile';
        return this.http.post(me, { emergencymobile: emergencymobile }).map(this.extractData);
    }

    uploadAvator(serverid: String): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/SetAvator';
        return this.http.post(me, { serverid: serverid }).map(this.extractData);
    }



    SetDoorLockAvator(serverid: String, imgid: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/SetDoorLockAvator';
        return this.http.post(me, { serverid: serverid, imgid: imgid }).map(this.extractData);
    }
    //获取用户信息
    GetUserInfo(): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/me';
        return this.http.post(me, {}).map(this.extractData);
    }
    //家庭列表
    GetFamilies(): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Family/GetFamilys';
        return this.http.post(me, {}).map(this.extractData);
    }
    //GetFamilyDevices
    GetFamilyDevices(): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Family/GetFamilyDevices';
        return this.http.post(me, {}).map(this.extractData);
    }
    //添加
    CreateFamily(name: string, address: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Family/CreateFamily';
        return this.http.post(me, { name: name, address: address }).map(this.extractData);
    }
    //重命名
    RenameFamily(familyId: number, name: string, address: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Family/RenameFamily';
        return this.http.post(me, { familyId: familyId, name: name, address: address }).map(this.extractData);
    }
    //删除
    DeleteFamily(familyId: number): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Family/DeleteFamily/' + familyId;
        return this.http.post(me, { familyId: familyId }).map(this.extractData);
    }
    //验证
    IsValid(familyId: number, name: string): boolean {
        //验证
        return true;
    }


    //获取微信config
    getWeixinConfig(url: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/weixin/JsSdk';
        return this.http.post(me, { url: url }).map(this.extractData);
    }
    //设置管理员权限
    SetFamilyManage(username: string, familyid: number): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Family/SetFamilyManage';
        return this.http.post(me, { username: username, familyid: familyid }).map(this.extractData);
    }
    ////////////家人 
    //获取家庭信息
    GetFamilyMember(memberId: number): any {
        // let f = this.channelService.hubProxy.invoke("GetFamily", familyId);
        // return f as FamilyModel;
    }


    //添加
    EditRelations(callname: string, username: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/EditRelations';
        return this.http.post(me, { callname: callname, username: username }).map(this.extractData);
    }
    //添加
    AddRelations(callname: string, phonenumber: string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/CreateRelations';
        return this.http.post(me, { callname: callname, phonenumber: phonenumber }).map(this.extractData);
    }
    //删除
    DeleteRelation(username: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Account/DeleteRelations';
        return this.http.post(me, { username: username }).map(this.extractData);

    }
    //获取家人的信息
    GetRelationInfo(username: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Family/getrelationinfo';
        return this.http.post(me, { username: username }).map(this.extractData);

    }
    //对某个家人取消分享设备
    CancelShared(username: string, device_id: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Account/cancelshared';
        return this.http.post(me, { username: username, device_id: device_id }).map(this.extractData);

    }
    //取消被分享的设备
    CancelSharededDevice(device_id: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Account/cancelshareded';
        return this.http.post(me, { device_id: device_id }).map(this.extractData);

    }
    //分享设备
    ShareDevices(username: string, device_ids: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Family/sharedevices';
        return this.http.post(me, { username: username, device_ids: device_ids }).map(this.extractData);

    }
    //分享设备
    ShareDeviceToUsers(usernames: string, device_id: string): any {

        let me = CONFIGURATION.baseUrls.server + '/api/Family/sharedevicestousers';
        return this.http.post(me, { usernames: usernames, device_id: device_id }).map(this.extractData);

    }

    //获取用户对应的家人信息
    GetUserFamilyInfo(username: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/account/getuserinfo/' + username;
        return this.http.get(url).map(this.extractData);
    }

    //获取所有相关的设备 {owndevices:[],shareddevices:[]}
    GetDevices() {
        let url = CONFIGURATION.baseUrls.server + '/api/device/FindMyAllDevices';
        return this.http.post(url, {}).map(this.extractData);
    }
    //获取指定设备的分享信息 
    GetSharedDevice(device_id: string) {
        let url = CONFIGURATION.baseUrls.server + '/api/device/getshareddevice/' + device_id;
        return this.http.get(url).map(this.extractData);
    }

    //获得分享状态 
    // GetShareInfo() {
    //     let url = CONFIGURATION.baseUrls.server + '/api/Account/GetShareInfo';
    //     return this.http.post(url, {}).map(this.extractData);
    // }

    //打开或关闭分享  1表示打开,0表示关闭
    // EditShareState(allowOrDeny: number) {
    //     let url = CONFIGURATION.baseUrls.server + '/api/Account/EditShareState/' + allowOrDeny;
    //     return this.http.post(url, {}).map(this.extractData);
    // }
    //打开或关闭分享  1表示打开,0表示关闭
    IsDemoLogin() {
        let url = CONFIGURATION.baseUrls.server + '/api/Account/IsDemoLogin';
        return this.http.post(url, {}).map(this.extractData);
    }
    //打开或关闭分享  1表示打开,0表示关闭
    ResetShareQr() {
        let url = CONFIGURATION.baseUrls.server + '/api/Account/ResetShareQr';
        return this.http.post(url, {}).map(this.extractData);
    }

    //获取好友详细信息
    GetRelations() {
        let url = CONFIGURATION.baseUrls.server + '/api/account/getusers';
        return this.http.get(url).map(this.extractData);
    }

}



