import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the LoginProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class LoginProvider {

  constructor(private http: HttpclientProvider) {
    }
    // testSignalr():void {
    //   this.channelService.hubProxy.invoke("GetFamilys");
    // }

    //登录发送短信验证码 scene //场景id:1注册时首次验证手机，2修改绑定手机，3验证新手机{token = "13413"}
    LoginSendCode(phonenumber: string, scene: number): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/LoginSendCode';
        return this.http.post(me, { mobile: phonenumber, scene: scene }).map(this.extractData);
    }
    //发送短信验证码 scene //场景id:1注册时首次验证手机，2修改绑定手机，3验证新手机
    Login(code: string, scene: number, token: string,nickname:string,unionid:string,avator:string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/Account/Login';
        return this.http.post(me, { code: code, scene: scene, token: token ,nickname:nickname,avator:avator,unionid:unionid }).map(this.extractData);
    }
    //微信授权登录
    WeiXinLogin(code: string, state: string,country:string): any {
        let me = CONFIGURATION.baseUrls.server + '/api/weixin/WeiXinLogin';
        return this.http.post(me, { code: code, state: state,country:country  }).map(this.extractData);
    } 
    //获取当前用户信息  /api/account/me 以后用这个接口
    ///api/weixin/me 过时
    getMyInfo(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/account/me';
        return this.http.post(url, null).map(this.extractData);
        // let headers = new Headers();
        // let token = this.getToken();
        // headers.append('Authorization', token);
        // headers.append('Access-Control-Allow-Origin', '*');
        // return this.http.post(me, null, { headers: headers }).map(this.extractData);//.catch(this.handleError);
    }

    updateUserFamilyId(familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/account/updateuserfamilyid/' + familyId;
        return this.http.get(url).map(this.extractData);
    }
    //检测token是否已过期
    checktoken():any{
         let url = CONFIGURATION.baseUrls.server + '/api/account/checktoken';
        return this.http.get(url).map(this.extractData);
    }

    //获取用户家数据
    getFamily(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/family/GetFamilys';
        return this.http.post(url, null).map(this.extractData);
    }

    //获取用户房间数据
    getRoom(familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/room/findrooms/' + familyId;
        return this.http.post(url, null).map(this.extractData);
    }

    //获取所有产品数据
    getFindProductAll(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/product/findall';
        return this.http.post(url, null).map(this.extractData);
    }

    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
    //获取登录环境
    getLoginEnvironment(): any {
        let url = CONFIGURATION.baseUrls.server + '/api/account/GetLoginEnvironment';
        return this.http.post(url, null).map(this.extractData); 
    }
    //处理AIUI智能
    InvokeAiui(familyId: number, data: any): any {
      let url = CONFIGURATION.baseUrls.server + '/api/aiui/InvokeAiui/'+familyId;
      return this.http.post(url, data).map(this.extractData);
    }
    InvokeBaidu(text: String, session_id: String): any {
        let url = CONFIGURATION.baseUrls.server + '/api/baidu/InvokeUnit?text='+text+"&session_id="+session_id;
        return this.http.post(url, {}).map(this.extractData);
      }
    GetBgJson(familyId: number):any{
        let url = "https://oss.zhijiaiot.com/assets/demo/"+familyId+"/bg.json?t="+new Date().getTime();
        return this.http.get(url).map(this.extractData);
    }
    GetSceneJson(familyId: number):any{
        let url = "https://oss.zhijiaiot.com/assets/demo/"+familyId+"/scene.json?t="+new Date().getTime();
        return this.http.get(url).map(this.extractData);
    }

    redirectToBind(): void {
        //获取当前路由
        //  let locaRouter = this._router.url;
        //获取当前url地址
        // let locaUrl = location.host;
        let redirect: string;
        // let ruturnHref: string;
        // ruturnHref = encodeURIComponent("http://" + locaUrl + "/#/autologin");
        console.log(location.href);
        redirect = CONFIGURATION.baseUrls.server + '/api/weixin/redirecttobind?returnUrl=' + encodeURIComponent(location.href);
        // if (locaRouter.length > 1) {//带路由页面
        //     ruturnHref = encodeURIComponent("http://" + locaUrl + "/#/autologin?targe=" + locaRouter);
        //     redirect = CONFIGURATION.baseUrls.server + '/api/weixin/redirecttobind?returnUrl=' + ruturnHref;
        // } else {
        //     ruturnHref = encodeURIComponent("http://" + locaUrl + "/#/autologin");
        //     redirect = CONFIGURATION.baseUrls.server + '/api/weixin/redirecttobind?returnUrl=' + ruturnHref;
        // }
        location.href = redirect;
       
    };

    redirectToPath(path:string): void {
        let redirect: string;
        redirect = CONFIGURATION.baseUrls.server + "/app/" + path;
        location.href = redirect;
       
    };
}



