import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the LogProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class LogProvider {

  constructor(private http: HttpclientProvider) {
    }

    //获取锁的日志信息
    //device_id 设备ID take 显示记录条数 skip当前第几页
    FindDoorLockLog(device_id: string, take: number, skip: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/log/findmanydevicelog';
        return this.http.post(url, { device_id: device_id, take: take, skip: skip }).map(this.extractData);
    }

    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



