import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the SceneProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class SceneProvider {

 constructor(private http: HttpclientProvider) {


    }

    ModifyIsHome(sceneId: number, is_anfang: boolean, isHome: boolean, familyId: number) {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/ModifyIsHome';
        return this.http.post(url, { sceneId: sceneId, is_anfang: is_anfang, isHome: isHome, familyId: familyId }).map(this.extractData);
    }

    AddScene(familyId: number, name: string, jobs: Array<any>, isSendMsg: boolean): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/AddScene';
        var jobsJsonStr = JSON.stringify(jobs);
        return this.http.post(url, { familyId: familyId, name: name, jobs: jobsJsonStr, is_send_msg: isSendMsg }).map(this.extractData);
    }

    ModifyScene(scene_id: number, jobs: Array<any>, name: string, isSendMsg: boolean, familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/ModifyScene';
        var jobsJsonStr = JSON.stringify(jobs);
        return this.http.post(url, { scene_id: scene_id, jobs: jobsJsonStr, name: name, is_send_msg: isSendMsg, familyId: familyId }).map(this.extractData);
    }

    initScene(familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/InitScene';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }

    DelScene(scene_id: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/DelScene';
        return this.http.post(url, { scene_id: scene_id }).map(this.extractData);

    }

    GetScene(familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/FindScene';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }

    GetSceneAndJob(sceneId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/GetSceneAndJob';
        return this.http.post(url, { sceneId: sceneId }).map(this.extractData);
    }

    GetSceneJob(sceneId:number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/GetSceneJob';
        return this.http.post(url, { sceneId: sceneId }).map(this.extractData);
    }

    ExcuteSceneJob(sceneId: number, familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/ExcuteScene';
        return this.http.post(url, { sceneId: sceneId, familyId: familyId }).map(this.extractData);
    }

    SetAnfang(name: string, familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/SetAnfang';
        return this.http.post(url, { name: name, familyId: familyId }).map(this.extractData);
    }

    PreExcuteSceneJob(jobs: Array<any>, familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/PreExcuteSceneJob';
        var jobsJsonStr = JSON.stringify(jobs);
        return this.http.post(url, { jobs: jobsJsonStr, familyId: familyId }).map(this.extractData);
    }

    FindJobs(productId: string, deviceId: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/FindJobs';
        return this.http.post(url, { productId: productId, deviceId: deviceId }).map(this.extractData);
    }

    FindDevices(familyId: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/FindDevices';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }
    FindIndexListDate(id:number) {
        let url = CONFIGURATION.baseUrls.server + '/api/Family/findindexlistdate/'+id;
        return this.http.get(url).map(this.extractData);
    }

    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
}



