import { Injectable } from '@angular/core';
import { Platform, AlertController } from 'ionic-angular';
import { AppVersion } from '@ionic-native/app-version';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener'
import { FileTransfer, FileTransferObject  } from '@ionic-native/file-transfer';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { HttpclientProvider } from '../httpclient/httpclient';
import { CONFIGURATION } from '../constants/constants';

/*
  Generated class for the NativeServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/


// @NgModule({
//   providers: [AppVersion]
// })

@Injectable()
export class NativeServiceProvider {

  constructor(private platform: Platform,
    private alertCtrl: AlertController,
    private transfer: FileTransfer,
    private appVersion: AppVersion,
    private file: File,
    private fileOpener: FileOpener,
    private inAppBrowser: InAppBrowser,
    private http: HttpclientProvider) {
  }

  APP_DOWNLOAD: string = null;//app下载地址


//返回手机类型 0,Android  1,Ios  -1,未知
iosAndAndroid(): number {
  if (this.isAndroid())
      return 0;
  else if (this.isIos())
      return 1;
  else
      return -1;
}


  /**
  * 检查app是否需要升级
  */
  detectionUpgrade() {
    //这里连接后台获取app最新版本号,然后与当前app版本号(this.getVersionNumber())对比
    //版本号不一样就需要申请,不需要升级就return
    //alert(this.getVersionNumber());

    this.getVersionNumber().then(result => {
      console.log("APP版本：" + result);
      if (result != null && result != "") {
        let istype = "pad";
        // if (this.isAndroid())
        //   istype = "android";
        // else if (this.isIos())
        //   istype = "ios";
        if (istype != null && istype != "") {
          console.log("istype:" + istype);
          this.getNewVersion(istype).subscribe(newResult => {
            console.log(newResult);
            if (newResult.data != null) {//version = version, download = download
              if (newResult.data.version != null && newResult.data.version != "" && newResult.data.version != result) {
                console.log("服务器版本：" + newResult.data.version);
                if (newResult.data.download != null && newResult.data.download != "") {
                  this.APP_DOWNLOAD = newResult.data.download;
                  this.LevelApp();
                }
              }
            }
          });
        }
      }
    });
  }
  LevelApp() {
    this.alertCtrl.create({
      title: '升级',
      subTitle: '发现新版本,是否立即升级？',
      buttons: [{ text: '取消' },
      {
        text: '确定',
        handler: () => {
          this.downloadApp();
        }
      }
      ]
    }).present();
  }

  /**
  * 下载安装app
  */
  downloadApp() {
    if (this.isAndroid()) {
      let alert = this.alertCtrl.create({
        title: '下载进度：0%',
        enableBackdropDismiss: false,
        buttons: ['后台下载']
      });
      alert.present();

      const fileTransfer: FileTransferObject = this.transfer.create();
      const apk = this.file.externalRootDirectory + 'android.apk'; //apk保存的目录

      fileTransfer.download(this.APP_DOWNLOAD, apk).then(() => {
        // console.log('window');
        // console.log(window);
        // console.log(window['install']);
        // window['install'].install(apk.replace('file://', ''));
        this.fileOpener.open(apk, 'application/vnd.android.package-archive').then(() =>{  
          console.log('File is opened')  
      }).catch(e => {  
          console.log('Error openening file', e)  
      });  
      });

      fileTransfer.onProgress((event: ProgressEvent) => {
        let num = Math.floor(event.loaded / event.total * 100);
        if (num === 100) {
          alert.dismiss();
        } else {
          let title = document.getElementsByClassName('alert-title')[0];
          title && (title.innerHTML = '下载进度：' + num + '%');
        }
      });
    }
    if (this.isIos()) {
      this.openUrlByBrowser(this.APP_DOWNLOAD);
    }
  }

  /**
  * 通过浏览器打开url
  */
  openUrlByBrowser(url: string): void {
    this.inAppBrowser.create(url, '_system');
  }

  /**
  * 是否真机环境
  * @return {boolean}
  */
  isMobile(): boolean {
    return this.platform.is('mobile') && !this.platform.is('mobileweb');
  }

  /**
  * 是否android真机环境
  * @return {boolean}
  */
  isAndroid(): boolean {
    return this.isMobile() && this.platform.is('android');
  }

  /**
  * 是否ios真机环境
  * @return {boolean}
  */
  isIos(): boolean {
    return this.isMobile() && (this.platform.is('ios') || this.platform.is('ipad') || this.platform.is('iphone'));
  }

  /**
  * 获得app版本号,如0.01
  * @description  对应/config.xml中version的值
  * @returns {Promise<string>}
  */
  getVersionNumber(): Promise<string> {
    return new Promise((resolve) => {
      this.appVersion.getVersionNumber().then((value: string) => {
        resolve(value);
      }).catch(err => {
        console.log('getVersionNumber:' + err);
      });
    });
  }

  getNewVersion(type: string): any {
    let me = CONFIGURATION.baseUrls.server + '/api/log/FindAppVersion/' + type;
    return this.http.get(me).map(this.extractData);
  }
  extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}



