import { Directive, ElementRef, Input } from "@angular/core";
@Directive({
    selector: '[myWidth]'
})
export class MyStyleWidthDirective {

    constructor(private el: ElementRef) { }
   @Input() set myWidth(value: number) {
         this.el.nativeElement.style.width = value+"%";
    }
}


