import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the LechengProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class LechengProvider {

  constructor(private http: HttpclientProvider) {
        
        
    }

    savePTZ(deviceId, degreesId, degreesTitle, hDegrees, vDegrees):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/SavePTZ';
        
        return this.http.post(url, { degreesId: degreesId, degreesTitle:degreesTitle, deviceId: deviceId, hDegrees:hDegrees, vDegrees:vDegrees }).map(this.extractData);
    }

    getPTZ(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/FindPTZ';
        
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    removePTZ(deviceId, degreesId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/RemovePTZ';
        
        return this.http.post(url, { degreesId: degreesId, deviceId: deviceId }).map(this.extractData);
    }

    GetUserToken():any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetUserToken';
        return this.http.post(url, { }).map(this.extractData);
    }

    BindSms():any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindSms';
        return this.http.post(url, {  }).map(this.extractData);
        
    }

    BindApp(smsCode):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindApp';
        return this.http.post(url, { smsCode: smsCode }).map(this.extractData);
    }

    BindAppNoSms():any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindAppNoSms';
        return this.http.post(url, { }).map(this.extractData);
    }

    BindDevice(deviceId, productId, familyId, roomId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindDevice';
        return this.http.post(url, { deviceId: deviceId, productId:productId, familyId:familyId, roomId:roomId }).map(this.extractData);
    }

    UnBindDevice(deviceId, sn):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/UnBindDevice';
        return this.http.post(url, { deviceId: deviceId, sn:sn }).map(this.extractData);
    }

    //在线状态
    DeviceOnline(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/DeviceOnline';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    //呼吸灯状态
    BreathingLightStatus(deviceId) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BreathingLightStatus';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    //设置呼吸灯状态
    ModifyBreathingLight(deviceId, status) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/ModifyBreathingLight';
        return this.http.post(url, { deviceId: deviceId, status:status }).map(this.extractData);
    }

    //拍照
    SetDeviceSnap(deviceId, sn) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/SetDeviceSnap';
        return this.http.post(url, { deviceId: deviceId, sn:sn }).map(this.extractData);
    }
    //拍照
    DelPicture(deviceId, id) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/DelPicture';
        return this.http.post(url, { deviceId: deviceId, id:id }).map(this.extractData);
    }
    //云台控制
    controlptz(deviceId, direct, hDegrees, vDegrees) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/ControlPTZ';
        return this.http.post(url, { deviceId: deviceId, direct:direct, hDegrees:hDegrees, vDegrees:vDegrees }).map(this.extractData);
    }

    GetLCPictureByDate(deviceId:string, type:number, date:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetLCPictureByDate';
        
        return this.http.post(url, { deviceId: deviceId,type:type, date:date }).map(this.extractData);
    }

    GetCameraList(deviceId:string, type:number, take:number = 10, skip:number = 0):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/FindCameras';
        
        return this.http.post(url, { deviceId: deviceId,type:type, take:take, skip:skip }).map(this.extractData);
    }

    GetCameraListBydate(deviceId:string, type:number,date:string, take:number = 10, skip:number = 0):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetCameraPictureByDate';
        
        return this.http.post(url, { deviceId: deviceId,date:date, type:type, take:take, skip:skip }).map(this.extractData);
    }

    GetCameraPictureGroupByDate(deviceId:string, type:number, take:number = 10, skip:number = 0):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetCameraPictureGroupByDate';
        
        return this.http.post(url, { deviceId: deviceId,type:type, take:take, skip:skip }).map(this.extractData);
    }

    //获取动检计划
    GetAlarmInfo(deviceId:string, sn:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetAlarmInfo';
        
        return this.http.post(url, { sn: sn, deviceId:deviceId }).map(this.extractData);
    }

    //设置动检开关
    SetAlarmStatus(sn:string, isOpen:boolean, deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/SetAlarmStatus';
        
        return this.http.post(url, { sn: sn, isOpen:isOpen, deviceId:deviceId }).map(this.extractData);
    }

    //设置动检计划 index=-1 为添加
    SetAlarmPlan(sn:string, index:number, period, beginTime, endTime, deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/SetAlarmPlan';
        
        return this.http.post(url, { sn: sn, index:index,period:period, beginTime:beginTime, endTime:endTime,deviceId:deviceId  }).map(this.extractData);
    }

    //删除动检计划
    delAlarmPlan(sn:string, index:number, deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/DelAlarmPlan';
        
        return this.http.post(url, { sn: sn, index:index, deviceId:deviceId }).map(this.extractData);
    }

    //获取翻转状态
    GetReverseStatus(sn:string, deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/FrameReverseStatus';
        
        return this.http.post(url, { sn: sn, deviceId:deviceId }).map(this.extractData);
    }

    //获取云存储信息
    getStorageStrategy(deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/getStorageStrategy';
        
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    //设置翻转状态
    SetReverseStatus(sn:string, isReverse:boolean, deviceId:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/ModifyFrameReverseStatus';
        
        return this.http.post(url, { sn: sn, isReverse:isReverse, deviceId:deviceId }).map(this.extractData);
    }

    UnBindDeviceByPhone(phoneNum, sn):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/UnBindDeviceByPhone';
        return this.http.post(url, { phoneNum: phoneNum, sn:sn }).map(this.extractData);
    }

    ReBindDevice(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/ReBindDevice';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    BindSmsByDevice(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindSmsByDevice';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    BindAppByDevice(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindAppByDevice';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    BindLiveByDevice(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/BindDeviceLive';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    QueryLiveStatus(deviceId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/QueryLiveStatus';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    OpenStorageStrategy(deviceId:string, type:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/OpenStorageStrategy';
        return this.http.post(url, { deviceId: deviceId, type:type }).map(this.extractData);
    }

    //0 正常绑定 -1 没有被绑定  -2绑定到其他用户 -3 调用失败 其他错误
    CheckDeviceBindOrNot(deviceId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/CheckDeviceBindOrNot';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    //设备token
    GetDeviceToken(deviceId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/GetDeviceToken';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    AddCheckBind(sn:string) {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/AddCheckBind';
        return this.http.post(url, { sn: sn }).map(this.extractData);
    }

    
    IsBindApp(deviceId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/IsBindApp';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    AlarmCallbackTest(deviceId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Lecheng/AlarmCallbackTest';
        return this.http.post(url, { deviceId: deviceId }).map(this.extractData);
    }

    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
}



