import { Pipe, Injectable, PipeTransform } from "@angular/core";
import { SignalrWindow } from "../providers/constants/constants";

@Pipe({
    name: 'host'
})



@Injectable()
export class Host implements PipeTransform {

    navtype: number = 0;
    constructor(private window: SignalrWindow) {
        this.navtype = this.inApp();
    }
    transform(value: string): string {
        if (value === null || value === "" || value === undefined) {
            if (this.navtype === 2)
                return (`assets/img/user_profile_default.png`);
            else
                return (`${this.window.nativeWindow.config.cdn}assets/img/user_profile_default.png`);
        }
        else {
            if (this.navtype === 2)
                return (`assets/img/${value}`);
            else
                return (`${this.window.nativeWindow.config.cdn}assets/img/${value}`);
        }
    }


    inApp(): number {
        //0 微信 1.浏览器  2 app
        var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
        if (ua.indexOf("micromessenger") > -1) {
            return 0;
        }
        else if (location.href.toLowerCase().indexOf('http') == 0) {
            return 1;
        }
        else {
            return 2;
        }
    }
}


