import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { HttpclientProvider } from "../httpclient/httpclient";
import { CONFIGURATION } from "../constants/constants";

/*
  Generated class for the DemoproviderProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class DemoproviderProvider {

  constructor(private http: HttpclientProvider) { }

  //控制设备
  Send(deviceid: string, type: number, command: string): any {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/send';
    return this.http.post(url, { deviceid: deviceid, type: type, command: command }).map(this.extractData);
  }
  //通知服务端给串口屏发送消息
  SendSerialPortData(deviceid: string, command: string, val: number = 0) {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/sendtohmi';
    return this.http.post(url, { deviceid: deviceid, command: command, val: val }).map(this.extractData);
  }

  SaveNewWind(deviceid: string, pm25: string,voc:string,temperature:string) {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/savesubpm25';
    return this.http.post(url, { deviceid: deviceid, pm25: pm25,voc:voc,temperature:temperature }).map(this.extractData);
  }
  GetOutWheather(deviceid:string){
     let url = CONFIGURATION.baseUrls.server + '/api/demodevice/getoutwheatherdata';
    return this.http.post(url, { deviceid: deviceid }).map(this.extractData);
  }

  //模型双色灯开关 
  SendLight(deviceid: string, code: number, stat: boolean): any {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/sendlight';
    return this.http.post(url, { deviceid: deviceid, code: code, stat: stat }).map(this.extractData);
  }

  //获取网关中的设备信息
  FindByIdDemoDevice(deviceid: string, type: number): any {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/FindByIdDemoDevice';
    return this.http.post(url, { deviceid: deviceid, type: type }).map(this.extractData);
  }
  //改变灯带渐变时间
  ChangeLightChangeTime(deviceId: string, time: number,rtype:string) {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/changelighttime';
    return this.http.post(url, { deviceId: deviceId, time: time,rtype:rtype }).map(this.extractData);
  }
  //获取日志信息
  //device_id 设备ID take 显示记录条数 skip当前第几页
  FindDemoLog(device_id: string, type: number, take: number, skip: number): any {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/FindDemoLog';
    return this.http.post(url, { device_id: device_id, type: type, take: take, skip: skip }).map(this.extractData);
  }
  //获取模型网关下的设备
  FindDemoIdDevice(device_id: string, family_id: number): any {
    let url = CONFIGURATION.baseUrls.server + '/api/demodevice/FindDemoIdDevice';
    return this.http.post(url, { device_id: device_id, family_id: family_id}).map(this.extractData);
  }

  extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}



