import { Pipe, Injectable, PipeTransform } from "@angular/core";
@Pipe({
    name: 'listfilter'
})

//过虑设备
@Injectable()
export class ListFilter implements PipeTransform {
    transform(items: any[], field: string, type: number): any[] {
        if (!items) return [];
        switch (type) {
            case 0:
                return items.filter(it => it[field] != 1 && it['main_device_id'] === null || it['main_device_id'] === "");
            case 1:
                return items.filter(it => it[field] != 1 && it[field] != 2);
            case 2:
                return items.filter(it => it[field] != 2);
            default:
                break;
        }
    }
}


