import { Injectable } from '@angular/core';
import { HttpclientProvider } from '../httpclient/httpclient';
import 'rxjs/add/operator/map';
//import { LocalstorageProvider } from '../localstorage/localstorage';
//import { ChannelProvider } from '../channel/channel';
import { CONFIGURATION } from '../constants/constants';
import { AutoJobInfo } from '../../interfaces/smart.model';
/*
  Generated class for the AutojobProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class AutojobProvider {

  constructor(
      //private channelService: ChannelProvider, 
    //private ls: LocalstorageProvider, 
    private http: HttpclientProvider) {
        
        
    }

    ModifyIsHome(sceneId:number, isHome:boolean) {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/ModifyIsHome';
        return this.http.post(url, { sceneId: sceneId, isHome:isHome }).map(this.extractData);
    }

    AddScene(familyId:number, name:string, jobs:Array<any>):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/AddScene';
        var jobsJsonStr = JSON.stringify(jobs);
        return this.http.post(url, { familyId: familyId, name:name, jobs:jobsJsonStr }).map(this.extractData);
    }

    SaveJob(autojobinfo:AutoJobInfo, conditions:Array<any>, jobs:Array<any>):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/SaveJob';
        var autojobinfoStr = JSON.stringify(autojobinfo);
        var conditionsStr = JSON.stringify(conditions);
        var jobsStr = JSON.stringify(jobs);
        return this.http.post(url, { autojobinfo: autojobinfoStr, conditions:conditionsStr,jobs:jobsStr }).map(this.extractData);
    }

    initScene(familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/InitScene';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }

    DelAutoJob(id:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/DelAutoJob';
        return this.http.post(url, { id: id }).map(this.extractData);
        
    }

    GetAutoJobs(familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/GetAutoJobs';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }

    GetAutoJob(id:number, familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/GetAutoJob';
        return this.http.post(url, { id: id, familyId: familyId }).map(this.extractData);
    }

    GetSceneJob(familyId:number, sceneId):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/GetSceneJob';
        return this.http.post(url, { familyId: familyId, sceneId:sceneId}).map(this.extractData);
    }

    ExcuteSceneJob(sceneId:number, familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/ExcuteScene';
        return this.http.post(url, { sceneId:sceneId, familyId: familyId}).map(this.extractData);
    }

    PreExcuteSceneJob(jobs:Array<any>, familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/PreExcuteSceneJob';
        var jobsJsonStr = JSON.stringify(jobs);
        return this.http.post(url, { jobs:jobsJsonStr, familyId: familyId}).map(this.extractData);
    }

    FindJobs(productId:string, deviceId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/Scene/FindJobs';
        return this.http.post(url, { productId: productId, deviceId:deviceId }).map(this.extractData);
    }

    GetAutoEvent(deviceId:string, productId:string):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/GetAutoEvent';
        return this.http.post(url, { deviceId: deviceId, productId: productId}).map(this.extractData);
    }

    FindDevicesByEvent(familyId:number):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/FindDevicesByEvent';
        return this.http.post(url, { familyId: familyId }).map(this.extractData);
    }

    UpdateEnable(id:number, disabled:boolean):any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/UpdateEnable';
        return this.http.post(url, { id: id, disabled:disabled }).map(this.extractData);
    }



    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



