import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
import { Room } from '../../interfaces/room.model';
/*
  Generated class for the RoomProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class RoomProvider {


  constructor(private http: HttpclientProvider) {


  }

  AddRoomById(familyId: number, room: Room): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Room/CreateRoom';
    return this.http.post(url, { familyId: familyId, name: room.RoomName }).map(this.extractData);
  }

  Rename(familyId: number, id: string, name: string): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Room/RenameRoom';
    return this.http.post(url, { familyId: familyId, roomid: id, name: name }).map(this.extractData);

  }

  remove(id): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Room/DeleteRoom/' + id;
    return this.http.post(url, { room_id: id }).map(this.extractData);

  }

  GetRoomListById(familyId: string): any {
    let url = CONFIGURATION.baseUrls.server + '/api/Room/FindRooms/' + familyId;
    return this.http.post(url, { family_id: familyId }).map(this.extractData);
  }

  extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}



