import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { CONFIGURATION } from '../constants/constants';
import { HttpclientProvider } from '../httpclient/httpclient';
/*
  Generated class for the IntelligentProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class IntelligentProvider {

constructor(private http: HttpclientProvider) {}

    FindByDeviceIdAsync(device_id: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/smart/FindByDeviceIdAsync';
        return this.http.post(url, { device_id: device_id }).map(this.extractData);
    }

    //修改状态
    UpdateStat(id: string, enable: boolean) {
        let url = CONFIGURATION.baseUrls.server + '/api/smart/UpdateStat';
        return this.http.post(url, { id: id, enable: enable }).map(this.extractData);
    }


    //查询关联家人
    FindFamilyName(family_id: number): any {
        let url = CONFIGURATION.baseUrls.server + '/api/smart/FindFamilyName';
        return this.http.post(url, { family_id: family_id }).map(this.extractData);
    }


    Save(id: string, device_id: string, smart_item_id: number, notify: number, enable: boolean, wechat_users: string, tts_users: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/smart/Save';
        return this.http.post(url, { id: id, device_id: device_id, smart_item_id: smart_item_id, notify: notify, enable: enable, wechat_users: wechat_users, tts_users: tts_users }).map(this.extractData);
    }

    FindByRelationIntelligence(familyId: number, deviceId: string): any {
        let url = CONFIGURATION.baseUrls.server + '/api/AutoJob/GetAutoJobsByDevice';
        return this.http.post(url, { familyId: familyId, deviceId: deviceId }).map(this.extractData);
    }
    extractData(res: Response) {
        let body = res.json();
        return body || {};
    }

}



