import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule, HammerGestureConfig, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import { NoopAnimationsModule } from '@angular/platform-browser/animations';  
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { AutoFitLayout } from '../filter/auto-fit-layout';
//import { Filter } from '../filter/filter';//设备类型过滤
//import { Host } from '../filter/host';//组合图片地址
import { PipesModule } from './pipe.module';//组合图片地址

import { ExtendedUrlImg } from '../extended/extendedurlimg';//图片转换

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HttpModule } from '@angular/http'
import { AutojobProvider } from '../providers/autojob/autojob';
import { ChannelProvider } from '../providers/channel/channel';
import { LocaldataProvider } from '../providers/localdata/localdata';
import { LocalstorageProvider } from '../providers/localstorage/localstorage';
import { HttpclientProvider } from '../providers/httpclient/httpclient';
import { DelayProvider } from '../providers/delay/delay';
import { DoorlockProvider } from '../providers/doorlock/doorlock';
import { EquipmentProvider } from '../providers/equipment/equipment';
import { ExternalinterfaceProvider } from '../providers/externalinterface/externalinterface';
import { IntelligentProvider } from '../providers/intelligent/intelligent';
import { LechengProvider } from '../providers/lecheng/lecheng';
import { LogProvider } from '../providers/log/log';
import { LoginProvider } from '../providers/login/login';
import { RoomProvider } from '../providers/room/room';
import { SceneProvider } from '../providers/scene/scene';
import { TimingProvider } from '../providers/timing/timing';
import { UsercenterProvider } from '../providers/usercenter/usercenter'; 
import { ColorPickerService } from '../components/colorpicker/lib/color-picker.service';
import { SignalrWindow } from "../providers/constants/constants";
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { DemoproviderProvider } from '../providers/demoprovider/demoprovider';
import { NativeServiceProvider } from '../providers/native-service/native-service';
import {AppVersion} from '@ionic-native/app-version';
import {File} from '@ionic-native/file';
import {InAppBrowser} from '@ionic-native/in-app-browser';
import {FileTransfer} from '@ionic-native/file-transfer'; 
import {NativeAudio} from '@ionic-native/native-audio';
import {Media } from '@ionic-native/media';
import { Emitter } from '../event/eventemitter';
import { FileOpener } from '@ionic-native/file-opener';
import { AndroidPermissions } from '@ionic-native/android-permissions';
//import { SliderDirective, TextDirective, ColorPickerDirective } from '../components/colorpicker/lib/color-picker.directive';

export class MyHammerConfig extends HammerGestureConfig {
  overrides = <any>{
    // override hammerjs default configuration
    'pan': { threshold: 5 },
    'swipe': {
      velocity: 0.4,
      threshold: 20,
      direction: 31 // /!\ ugly hack to allow swipe in all direction
    }
  }
} 

@NgModule({
  declarations: [
    MyApp,
   // Host,
  //  Filter,
    AutoFitLayout
  ],
  imports: [
    BrowserModule,
    NoopAnimationsModule,
    HttpModule,
    PipesModule,
    IonicModule.forRoot(MyApp,{
      tabsHideOnSubPages: true,
      platforms: {
        android: {
          tabsHideOnSubPages: true,
          tabsHighlight: true,
          backButtonText: ''
        },
        ios: {
          tabsHideOnSubPages: true,
          tabsHighlight: true,
          backButtonText: ''
        }
      }
      //mode: 'md',
      //backButtonText: '返回'
    }), 
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp 
  ],
  providers: [
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    {
      provide: HAMMER_GESTURE_CONFIG,
      useClass: MyHammerConfig
    },AppVersion,File,InAppBrowser,FileTransfer,NativeAudio,Media,FileOpener,AndroidPermissions,
    StatusBar, SplashScreen,
    AutojobProvider,
    ChannelProvider,
    LocaldataProvider,
    LocalstorageProvider,
    HttpclientProvider,
    DelayProvider,
    DoorlockProvider,
    EquipmentProvider,
    ExternalinterfaceProvider,
    IntelligentProvider,
    LechengProvider,
    LogProvider,
    LoginProvider,
    RoomProvider,
    SceneProvider,
    TimingProvider,
    UsercenterProvider,
    SignalrWindow,
    Emitter,
    ExtendedUrlImg,
    ColorPickerService,
    BarcodeScanner,
    DemoproviderProvider, NativeServiceProvider 
  ]
})
export class AppModule { }



